
import { defineComponent } from 'vue'
import TheMainHeader from '@/components/TheMainHeader.vue'
import TheSidebar from '@/components/TheSidebar.vue'
import VTaskBorderCard from '@/components/VTaskBorderCard.vue'
import TheOverlay from '@/components/UI/TheOverlay.vue'

export default defineComponent({
  components: {
    TheMainHeader,
    TheSidebar,
    TheOverlay,
    VTaskBorderCard
  },
  data() {
    return {
      showModal: false,
      showAlert: false,
      tasks: { id: 1, completedTask: 372, openTasks: 9 }
    }
  },
  methods: {
    showOverlay(visible: boolean) {
      this.showModal = visible
    },
    hideOverlay(visible: boolean) {
      this.showModal = visible
    },
    changeMessage() {
      this.showAlert = true
    },
    changeTaskNumber() {
      if (this.tasks.openTasks !== 0) {
        this.tasks.completedTask = this.tasks.completedTask + 1
        this.tasks.openTasks = this.tasks.openTasks - 1
        if (this.tasks.openTasks === 0) {
          this.changeMessage()
        }
      }
    }
  }
})
