
import { defineComponent } from 'vue'
import CurrentUser from '@/components/User/CurrentUser.vue'
export default defineComponent({
  name: 'Sidebar',
  components: { CurrentUser },
  props: {
    tasks: {
      type: Object
    }
  },
  data() {
    return {}
  },
  methods: {
    showModal() {
      this.$emit('showModal', true)
    },
    goTasks(numberOpenTask: number) {
      if (numberOpenTask !== 0) {
        this.$router.push('/')
      }
    }
  }
})
